},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center z-2 mb-4 mb-lg-5"},[_c('div',{staticClass:"col-sm-8 col-lg-4"},[_c('figure',[_c('img',{staticClass:"mb-3 mb-lg-0",attrs:{"src":require("@/assets/curso/temas/tema4/35.png"),"alt":"Recorrido del aceite"}})])]),_c('div',{staticClass:"col-lg-8 p-4 p-lg-5 pb-lg-4 quote-gris"},[_c('h4',[_vm._v("Función")]),_c('p',{staticClass:"mb-0"},[_vm._v("La función principal del volante de inercia es almacenar energía cinética durante las carreras de potencia (cuando los pistones generan torque) y liberar esa energía durante las carreras no motorizadas (escape, admisión, compresión). Esto ayuda a suavizar las fluctuaciones de velocidad angular del cigüeñal, proporcionando un giro más uniforme y reduciendo las vibraciones inherentes al funcionamiento de un motor alternativo. También facilita el arranque del motor y sirve como superficie de fricción para el embrague, permitiendo la conexión y desconexión entre el motor y la transmisión. En su periferia, lleva una corona dentada con la que engrana el piñón del motor de arranque para poner en marcha el motor.")])])])
